<template>
  <div>
    <editor-content :editor='editor' class='editor-content' />

    <editor-menu-bar :editor='editor' v-slot='{ commands, isActive }'>
      <div>
        <button :class='{ "is-active": isActive.bold() }' @click='commands.bold'>Bold</button>
        <button :class='{ "is-active": isActive.italic() }' @click='commands.italic'>Italic</button>
        <button
          type='button'
          :class='{ "is-active": isActive.strike() }'
          @click='commands.strike'
        >Strike</button>
        <button
          type='button'
          :class='{ "is-active": isActive.underline() }'
          @click='commands.underline'
        >Underline</button>
        <button
          :class='{ "is-active": isActive.heading({ level: 1 }) }'
          @click='commands.heading({ level: 1 })'
        >H1</button>
        <button
          :class='{ "is-active": isActive.heading({ level: 2 }) }'
          @click='commands.heading({ level: 2 })'
        >H2</button>
        <button
          :class='{ "is-active": isActive.heading({ level: 3 }) }'
          @click='commands.heading({ level: 3 })'
        >H3</button>
        <button
          type='button'
          :class='{ "is-active": isActive.blockquote() }'
          @click='commands.blockquote'
        >Blockquote</button>
        <button :class='{"is-active":isActive.bullet_list()}' @click='commands.bullet_list'>list</button>
        <button class='menubar__button' @click='showImagePrompt(commands.image)'>img</button>
      </div>
    </editor-menu-bar>

    <button>Save</button>
  </div>
</template>

<script>
// Import the editor
import { Editor, EditorContent, EditorMenuBar } from "tiptap";
import {
  Blockquote,
  CodeBlock,
  HardBreak,
  Heading,
  OrderedList,
  BulletList,
  ListItem,
  TodoItem,
  TodoList,
  Bold,
  Code,
  Italic,
  Link,
  Strike,
  Underline,
  History,
  Image,
} from "tiptap-extensions";

export default {
  components: {
    EditorContent,
    EditorMenuBar,
  },
  data() {
    return {
      editor: null,
    };
  },
  mounted() {
    this.editor = new Editor({
      extensions: [
        new Blockquote(),
        new CodeBlock(),
        new HardBreak(),
        new Heading({ levels: [1, 2, 3] }),
        new BulletList(),
        new OrderedList(),
        new ListItem(),
        new TodoItem(),
        new TodoList(),
        new Bold(),
        new Code(),
        new Italic(),
        new Link(),
        new Strike(),
        new Underline(),
        new History(),
        new Image(),
      ],
      content: "<p>This is just a boring paragraph</p>",
    });
  },

  methods: {
    showImagePrompt(command) {
      const src = prompt("Enter the url of your image here");
      if (src !== null) {
        command({ src });
      }
    },
  },
};
</script>

<style lang="scss">
.editor {
  &-content {
    font-size: 12px;
    width: 95%;
    margin-bottom: 2px;
    height: 200px;
    background-color: white;
    border: 1px solid rgb(231, 231, 231); 
    overflow: auto;
    border-radius: 10px;
    padding-left: 10px;
  }
}
</style>