<script>
export default {
  methods: {
    openGame() {
      const y = window.top.outerHeight / 2 + window.top.screenY - 550 / 2;
      const x = window.top.outerWidth / 2 + window.top.screenX - 800 / 2;
      window.open(
        "/game",
        "mywin",
        `toolbar=no, location=no, directories=no, status=no, menubar=no, scrollbars=no, resizable=no, copyhistory=no, width=${850}, height=${550}, top=${y}, left=${x}`
      );
    },
    async logout(){
      const res = await this.$http.post("/user/logout")
      this.$store.commit("user/setUser", res.data)
    }
  },
  computed:{
    user(){
      return this.$store.getters["user/userData"]
    },
    isVisible(){
      return this.user.rank.authLevel >= 0
    }
  },
  async created(){
    if(this.user.id!==null) return
    try {
      const res = await this.$http.get("/user")
      this.$store.commit("user/setUser", res.data)
    }catch(err){
      alert(err)
    }
  }
};
</script>

